const questions = [
    {m:"Which food does he prefer?",f:"Which food does she prefer?"},
    {m:"How does he get to school or work?",f:"How does she get to school or work?"}
];

export const stack = () => {
    return [
        {
            question:questions[1],
            items:["1f697.svg","1f68c.svg", "1f6b4.svg", "1f689.svg", "1f3c3.svg"],
            labels:["102", "84", "77", "43", "24"]
        },
        {
            question:questions[0],
            items:["1f354.svg","1f35f.svg", "1f369.svg", "1f368.svg", "1f37b.svg"],
            labels:["102", "84", "77", "43", "24"]
        }
    ];
}

export var users = {
    "user1": stack(),
    "user2": stack()
};



