import $ from 'jquery';
import 'jqHammer';
import {
    users,
    stack
} from './data';

const superlikeButton = (e) => {
    let self = e.currentTarget;
    let docIcon = $(self);
    docIcon.siblings('.doc-icon-dislike').removeClass('doc-icon-active');
    docIcon.toggleClass('doc-icon-active');
}

const dislikeButton = (e) => {
    let self = e.currentTarget;
    let docIcon = $(self);
    if (docIcon.siblings('.doc-icon-like').hasClass('doc-icon-active'))
        return;
    docIcon.siblings('.doc-icon-superlike').removeClass('doc-icon-active');
    docIcon.toggleClass('doc-icon-active');
}

const likeButton = (e) => {
    let self = e.currentTarget;
    let docSection = $(self).parents('.doc-section');
    let docContent = docSection.find('.doc-content');
    let docIcon = docSection.find('.doc-icon-like');
    let docTrans = docSection.find('.doc-transparent');
    let docHeart = docSection.find('.doc-heart');
    let docEmoji = docSection.find('.doc-emoji');
    docContent.unbind();
    docIcon.unbind();
    if (!docIcon.hasClass('doc-icon-active')) {
        docIcon.siblings('.doc-icon-dislike').removeClass('doc-icon-active');
        loadQuestion(e);
        docTrans.fadeToggle(200);
        docHeart.fadeToggle(200, () => {
            setTimeout(() => {
                docHeart.fadeToggle(200);
                docEmoji.slideToggle();
                docContent.bind("dblclick", likeButton);
                docIcon.bind("click", likeButton);
            }, 1000);
        });
    } else {
        let id = docSection.attr('id');
        users[id] = stack();
        docEmoji.hide();
        docTrans.hide();
        docContent.bind("dblclick", likeButton);
        docIcon.bind("click", likeButton);
    }
    docIcon.toggleClass('doc-icon-active');
    docHeart.toggleClass('is-active');
}

const nextQuestion = (e) => {
    let self = e.currentTarget;
    let docEmoji = $(self).parents('.doc-emoji');
    let time = 350;
    if (e.type == "swipe")
        time = 0;
    if (e.type == "click") {
        $(self).toggleClass('doc-top-emoji-active');
        let label = $(self).next(".doc-emoji-label").children("p");
        let current = parseInt(label.text());
        current = current + 1;
        label.text(current);
    }
    setTimeout(() => {
        docEmoji.slideUp(400, () => {
            $(self).toggleClass('doc-top-emoji-active');
            let empty = loadQuestion(e);
            if (empty)
                return;
            setTimeout(() => {
                docEmoji.slideDown();
            }, 400);
        });
    }, time);
}

const loadQuestion = (e) => {
    let self = e.currentTarget;
    let docSection = $(self).parents('.doc-section');
    let id = docSection.attr('id');
    let gender = docSection.attr('data-gender');
    let docEmoji = docSection.find('.doc-emoji');
    let head = users[id].pop();
    if (head == undefined) {
        users[id] = stack();
        let docTrans = $(self).parents('.doc-section').find('.doc-transparent');
        docEmoji.fadeOut(200);
        docTrans.fadeOut(200);
        return true;
    }
    let items = head.items;
    let question = ``;
    if (gender == "m")
        question = head.question.m
    else
        question = head.question.f
    let htmlContent = `<h3 class="doc-question-text">${question}</h3>`;
    items.forEach(currentItem => {
        htmlContent += `<div class="doc-top-emoji-div">
            <img class="doc-top-emoji doc-question-text" alt="", src="img/svg/${currentItem}" />
            <div class="doc-emoji-label"><p>${head.labels[items.indexOf(currentItem)]}</p></div>
            </div>`;
    });
    docEmoji.html(htmlContent);
    docEmoji.find(".doc-top-emoji").on("click", nextQuestion);
    docEmoji.find(".doc-question-text").hammer().on("swipe", nextQuestion);
    return false;
}

$(document).ready(() => {
    $(".doc-content").hammer().on('tap', likeButton);
    $(".doc-icon-like").on("click", likeButton);
    $(".doc-icon-dislike").on("click", dislikeButton);
    $(".doc-icon-superlike").on("click", superlikeButton);
});


